

const initialState = { token: null }

function main(state = initialState, action) {
    let nextState
    switch (action.type) {
        case 'ADD_TOKEN':
            nextState = {
                ...state,
                token: action.value
            }

            return nextState || state

            break;
        case 'DELETE_TOKEN':
            nextState = {
                ...state,
                token: null
            }

            return nextState || state

            break;

        default:
            return state
            break;
    }
}

export default main