import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import logoImg from "../assets/images/logo.png"
import background from "../assets/images/loginBackground.jpg"
import { Link } from 'react-router-dom/cjs/react-router-dom.min'
import { connect } from 'react-redux'
import api from "../api";
import jwt from 'jwt-decode'


function Login(props) {

    const [password, setPassword] = useState("")
    const [email, setEmail] = useState('')
    async function sendData() {
        const data = {
            password,
            email
        }
        try {
            let response = await api.post("/users/login", data)
            console.log(response.data);
            if (response.data && response.data.user_token) {

                const action = {
                    type: "ADD_TOKEN",
                    value: response.data.user_token
                }
                console.log(response.data.user_token);

                const user = jwt(response.data.user_token);
                console.log(user);
                props.dispatch(action)

                props.history.push("/")
            } else {
                console.log('test');
            }

        } catch (error) {
            console.error(error);
        }
    }
    return (
        <Container>
            <Wrapper>
                <Logo src={logoImg} />
                <FormContainer>
                    {/* <Title>Connexion</Title> */}
                    <Form
                        onSubmit={(props) => {
                            props.preventDefault()
                            sendData()
                        }}
                    >
                        <div>
                            <Label>Adresse mail</Label><br />
                            <Input onChange={({ target }) => { setEmail(target.value) }} type="email" required placeholder="Votre adresse mail" /><br />

                            <Label>Mot de passe</Label><br />
                            <Input onChange={({ target }) => { setPassword(target.value) }} type="password" required placeholder="Votre mot de passe" /><br />
                        </div>
                        <SubmitButton>Se connecter</SubmitButton>
                        <p>Vous n'avez pas de compte? <Link to='/registration' >Inscrivez vous</Link></p>
                    </Form>
                </FormContainer>
            </Wrapper>
        </Container>
    )
}


const Container = styled.div`
    background: url(${background}) no-repeat;
    background-size: cover;
    display: flex;
    flex: 1;
    height: 100vh;
`
const Wrapper = styled.div`
background-color: white;
display: flex;
justify-content: center;
align-items: center;
flex-direction: column;
/* background-color: rgba(254, 132, 14, .5); */

`
const Logo = styled.img`
width: 20vw;
`
const FormContainer = styled.div`
display: flex;
align-items: center;
flex-direction: column;
`
const Form = styled.form`
padding: 20px;
display: flex;
flex-direction: column;
align-items: center;
flex : 1;
`
const Input = styled.input`
background-color: rgba(0,0,0,.1);
border: none;
width: 25vw;
padding: 10px;
margin-top: 5px;
margin-bottom: 20px;
border-radius: 20px;
padding-left: 20px;
font-size: 1.01rem;
font-family: 'Poppins', sans-serif;
`
const Label = styled.label``
const Title = styled.p`
font-weight: 600;
font-size: 2rem;
color: ${props => props.theme.colors.primary};
`
const SubmitButton = styled.button`
background-color: ${props => props.theme.colors.primary};
color : white;
width: 300px;
`

const mapStateToProps = (state) => (state)

export default connect(mapStateToProps)(Login)
