import { Button, Typography } from '@material-ui/core'
import React, { useState } from 'react'
import jwtDecode from 'jwt-decode';
import styled from 'styled-components'
import { connect } from 'react-redux'
import api from "../api";

export const Profile = (props) => {

    const [password, setPassword] = useState("")


    let user = null
    props.token && (user = jwtDecode(props.token))

    async function generateKey() {
        // console.log("test");
        // return
        try {
            let _response = await api.post("/memberships/new", {
                left_id: user.id,
                right_id: 1,
                expired: true
            })
            let response = await api.post("/users/token", { email: user.email, username: user.username, password: password })
            console.log(response);
        } catch (error) {
            console.error(error);
        }
    }

    return (
        <div>
            <Typography variant={"h2"} >{user.username}</Typography>
            <Typography>Clé de l'API : { }</Typography>


            <form
                onSubmit={(props) => {
                    props.preventDefault()
                    generateKey()
                }}
            >
                <Label>Mot de passe</Label><br />
                <Input onChange={({ target }) => { setPassword(target.value) }} type="password" required placeholder="Votre mot de passe" /><br />
                <Button
                    variant="contained"
                    type='submit'
                    color="primary"
                    style={{ marginBottom: 10 }}
                    // className={classes.button}
                    onClick={() => { generateKey() }}
                >
                    Générer une clé
                </Button>
            </form>
        </div>
    )
}
const Input = styled.input`
background-color: rgba(0,0,0,.1);
border: none;
width: 25vw;
padding: 10px;
margin-top: 5px;
margin-bottom: 20px;
border-radius: 20px;
padding-left: 20px;
font-size: 1.01rem;
font-family: 'Poppins', sans-serif;
`
const Label = styled.label``
const mapStateToProps = (state) => (state)

export default connect(mapStateToProps)(Profile)
