import React, { useState } from 'react'
import styled from 'styled-components'
import Chip from '@material-ui/core/Chip';
import DeleteIcon from '@material-ui/icons/Delete';
import AddIcon from '@material-ui/icons/Add';
import Button from '@material-ui/core/Button';
import api from "../../api";
import { connect } from 'react-redux';


const Test = (props) => {

    const [currentText, setCurrentText] = useState('')
    const [sentences, setSentences] = useState([])

    async function calculate() {
        const config = {
            headers: { Authorization: `Bearer ${props.token}` }
        };

        try {
            let response = await api.post("/sentiment-analysis/sentiments", sentences, config)
            console.log(response);
        } catch (error) {
            console.error(error);
        }
    }

    return (
        <Container>
            <form>
                <Label>Ajouter une phrase à analyser</Label><br />
                <Input rows="5" value={currentText} onChange={({ target }) => { setCurrentText(target.value) }} type="text" required placeholder="Entrer une phrase" /><br />
                <Button
                    variant="contained"
                    color="primary"
                    style={{ marginBottom: 10 }}
                    // className={classes.button}
                    endIcon={<AddIcon />}
                    disabled={sentences.length == 5 || currentText == ""}
                    onClick={() => { setSentences([...sentences, currentText]); setCurrentText("") }}
                >
                    Ajouter
                </Button>
            </form>

            <ChipContainer>
                {
                    sentences.map((el, id) => (
                        <Chip
                            key={id}
                            label={el}
                            // onClick={handleClick}
                            onDelete={() => { setSentences(sentences.filter((el, _id) => (_id !== id))) }}
                            onClick={() => { setCurrentText(el) }}
                            deleteIcon={<DeleteIcon />}
                            variant="outlined"
                            style={{ marginBottom: 15 }}
                        />
                    ))
                }
            </ChipContainer>
            <Button
                variant="contained"
                color="secondary"
                style={{ marginBottom: 10 }}
                // className={classes.button}
                onClick={() => { calculate() }}
            >
                Calculer
            </Button>
        </Container>
    )
}
const Container = styled.div`
    display: flex;
    flex-direction: column;
    flex: 1;
`
const Input = styled.textarea`
background-color: rgba(0,0,0,.1);
border: none;
width: 25vw;
padding: 10px;
margin-top: 5px;
margin-bottom: 5px;
border-radius: 20px;
padding-left: 20px;
font-size: 1.01rem;
font-family: 'Poppins', sans-serif;
resize: none;
`
const Label = styled.label``
const ChipContainer = styled.div`
    display: flex;  
    flex-direction: column;
    `
const mapStateToProps = (state) => (state)

export default connect(mapStateToProps)(Test)
